

.form {
    font-family: 'Montserrat';
    display: inline-flex;
    flex-direction: column;
}

.input{
    float: right;
    width: 192px;
    height: 48px;
    font-size: 28px;
}

.div {
    display: inline;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.label {
    float: left;
    padding-right: 2rem;
    font-size: 32px;
}

.submit {
    width: 96px;
    position: relative;
    right: -40%;
    margin-top: 3rem;
    font-size: 24px;
}

.number {
    font-family: 'Montserrat';
    position: relative;
    right: 0%;
    margin-top: 4rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-left: 4rem;
    padding-right: 4rem;
    border: 9px solid black;


}